.Field {
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 0.25rem;
  width: 100%;
  max-width: 16rem;
  
  .Label {
    font-size: 0.875rem;
    line-height: 1.25rem;
    font-weight: 500;
    color: var(--color-gray-900);
  }
  
  .Input {
    box-sizing: border-box;
    padding-left: 0.875rem;
    margin: 0;
    border: 1px solid var(--color-gray-200);
    width: 100%;
    height: 2.5rem;
    border-radius: 0.375rem;
    font-family: inherit;
    font-size: 1rem;
    background-color: transparent;
    color: var(--color-gray-900);
  
    &:focus {
      outline: 2px solid var(--color-blue);
      outline-offset: -1px;
    }
  }
  
  .Error {
    font-size: 0.875rem;
    line-height: 1.25rem;
    color: var(--color-red-800);
  }
  
  .Description {
    margin: 0;
    font-size: 0.875rem;
    line-height: 1.25rem;
    color: var(--color-gray-600);
  }
}
